import { CqsSearchQuery } from "@rp/common/api-types"
import { isCqsQueryNotEmpty } from "./isCqsQueryNotEmpty"

export const cqsQueryLooksValid = ({ expression, sort, ranking, scope }: CqsSearchQuery) => {
  if (!isCqsQueryNotEmpty({ expression })) {
    return false
  }

  if (!sort || !ranking || !scope) {
    return false
  }

  return true
}
